import { Component } from 'react';
import Statistics from '../Statistics/Statistics';
import FeedbackOptions from '../FeedbackOptions/FeedbackOptions';
import styles from '../Statistics/Statistics.module.css';

class ComponenStatistics extends Component {

  state = {
    good: 0,
    neutral: 0,
    bad: 0
  };

  total = 0;
  positiveFeedback = 0;

  onAddition = name => {
    this.setState(state => ({
      [name]: Number(state[name] + 1),
    }));
    this.countTotalFeedback();
    this.countPositiveFeedbackPercentage();
  };

  countTotalFeedback = () => {
    this.setState(prevState => ({
      total: prevState.good + prevState.neutral + prevState.bad,
    }))
  }

  countPositiveFeedbackPercentage = () => {
    this.setState(prevState => ({
      positiveFeedback: Math.round((prevState.good / prevState.total) * 100)
    }))
  }
    
  render() {
    return (
      <>
          <h1 className={styles.title}>Please leave feedback</h1>
        
        <FeedbackOptions
          options="button"
          onLeaveFeedback={this.onAddition}
        />
        <div className={styles.counter}>
          <h2 className={styles.title}>Statistics</h2>
          <Statistics good={this.state.good}
           neutral={this.state.neutral}
           bad={this.state.bad}
           total={this.state.total}
           positiveFeedback={this.state.positiveFeedback} />
        </div>
      </>
    );
  }
}

export default ComponenStatistics;