import PropTypes from 'prop-types';
import styles from '../FeedbackOptions/FeedbackOptions.module.css';


function FeedbackOptions({ options, onLeaveFeedback }) {

    return (<>
        <button id="good" type={options} className={styles.btn} onClick={() => onLeaveFeedback('good')}>Good</button>
        <button id="neutral" type={options} className={styles.btn} onClick={() => onLeaveFeedback('neutral')}>Neutral</button>
        <button id="bad" type={options} className={styles.btn} onClick={() => onLeaveFeedback('bad')}>Bad</button>
    </>)
}

FeedbackOptions.propTypes = {
    options: PropTypes.string,
    onLeaveFeedback: PropTypes.func,
}

export default FeedbackOptions;